<template>
  <div id="ledgerAdd">
    <Modal
      v-model="build"
      title=""
      @on-cancel="reset"
    >
      <Form ref="addForm" :model="AddData" :rules="ruleValidate" :label-width="160" style="padding:0 10%;">
        <FormItem prop="name" label="">
          <Input v-model.trim="AddData.name" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="remark" label="">
          <Input v-model.trim="AddData.remark" type="textarea" :rows="4" placeholder="" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import {
  // 
  createAccount1
} from '@/api/remoteSensing'
export default {
  name: 'TaskAdd',
  data () {
    return {
      /*  */
      AddData: {
        name: '',
        remark: ''
      },
      /*  */
      ruleValidate: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      /*  */
      build: false
    }
  },
  mounted () {},
  methods: {
    /*  */
    addSubmit () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          createAccount1({
            data: {
              createBy: 1, // id
              name: this.AddData.name,
              remark: this.AddData.remark
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '' })
              this.$emit('getTableData')
              this.build = false
              this.AddData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          }).catch(() => {
            this.$Notice.error({ title: '' })
          })
        }
      })
    },
    // 
    reset () {
      this.AddData = {
        name: '',
        remark: ''
      }
      this.$refs.addForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
