<template>
  <div id="ledgerEdit">
    <Modal
      v-model="edit"
      title=""
      @on-cancel="reset">
      <Form ref="editForm" :model="EditData" :rules="EditRule" :label-width="160" style="padding:0 10%;">
        <FormItem prop="id" label="ID" style="display: none">
          <Input v-model.trim="EditData.id"/>
        </FormItem>
        <FormItem prop="name" label="">
          <Input v-model.trim="EditData.name" placeholder="" size="default"/>
        </FormItem>
        <FormItem prop="remark" label="">
          <Input v-model.trim="EditData.remark" type="textarea" :rows="4" placeholder="" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import {
  // 
  editAccount1
} from '@/api/remoteSensing'
export default {
  name: 'Taskedit',
  data () {
    return {
      /*  */
      EditRule: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      /*  */
      edit: false,
      /*  */
      EditData: {}
    }
  },
  props: {
    /*  */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  mounted () {},
  methods: {
    /*  */
    dataHandle () {
      this.EditData.id = this.data.id
      // this.EditData.name = this.data.name
      this.EditData.remark = this.data.remark
      this.$set(this.EditData, 'name', this.data.name)
    },
    /*  */
    editSubmit () {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          editAccount1({
            data: {
              // createBy: 1, // id
              id: this.EditData.id,
              name: this.EditData.name,
              remark: this.EditData.remark
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '' })
              this.$emit('getTableData')
              this.edit = false
              this.EditData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 
    reset () {
      this.EditData = {
        id: '',
        name: '',
        remark: ''
      }
      this.EditData = {}
      this.$refs.editForm.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
